import './DataWindow.css';
import React, { useRef, useState } from 'react';
import TemplateModal from '../../../../components/TemplateModal/TemplateModal';


function DataWindow({ data, text, btClass }) {

  const element = useRef();

  const [active, setActive] = useState(0);

  const convertCSVDataToHTMLTable = (data) => {
    data = data.split("\n");
    let table = craftTable(data);
    return table;
  }

  const craftTable = (data) => {
    return <table>{craftHeader(data[0])}{craftBody(data)}</table>
  }

  const craftHeader = (data) => {
    return <thead><tr>{data.split(`;`).map((cell, index) => <th>{cell.replace('_', ' ').toUpperCase()}</th>)}</tr></thead>;
  }

  const craftBody = (data) => {
    return <tbody>{data.map((row, index) => index > 0 ? craftBodyRow(row) : null)}</tbody>;
  }

  const craftBodyRow = (row) => {
    return <tr>{row.split(`;`).map((cell, index) => craftBodyCell(cell))}</tr>;
  }

  const craftBodyCell = (cell) => {
    return <td>{cell.match(/http/)?craftBodyHiperLinkCell(cell):cell}</td>
  }

  const craftBodyHiperLinkCell = (cell) => {
    return <a href={cell} target='_blank' rel="noreferrer">{cell}</a>
  }

  return (
    <div className="DataWindow">
      <a className={btClass+" HeavyLink"} onClick={() => setActive(active+1)} href='#' rel="noreferrer">{text}</a>
      <TemplateModal title={`Dados`} modalSize="xl" active={active}>

        {convertCSVDataToHTMLTable(data)}

      </TemplateModal>
    </div>
  )

}

export default DataWindow;